import React from 'react';
import { validateType } from 'ts-validate-type';

import './index.scss';

interface IButtonTheme {
    hoverButtonAnimation?: boolean;
}

interface IButton {
    onClick?: () => void;
    children: React.ReactNode;
    theme?: IButtonTheme;
}

function Button(props: IButton) {
    try {
        const myValue = validateType<string>('value');
        console.log('myValue', myValue);
    } catch (e) {
        console.log('e', e);
        // handle incorrect type
    }
    return (
        <div className={`buttonContainer ${props.theme?.hoverButtonAnimation ? 'hover_button_animation' : ''}`} onClick={props.onClick}>
            <span>{props.children}</span>
        </div>
    );
}

export default Button;
